
.title{width:100%;height:50px;color:#fefefe;background-color:#4ab9f1;padding:15px 10px 0
}
.title .back{width:19px;height:24px;background:url("assets/back.png") no-repeat center;background-size:9px 14px;float:left;padding:5px;position:absolute
}
.title h1{font-size:18px;line-height:24px
}
#app{font-family:'Avenir',Helvetica,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;position:relative;width:100%;height:100%
}
.flex-box-column{-ms-flex-direction:column;flex-direction:column
}
.flexBox{display:box;display:-ms-flexbox;display:flex;display:-webkit-flex
}
html,body{width:100%;height:100%
}
html,body{margin:0;padding:0
}
h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,cite,code,del,dfn,em,img,q,s,small,strike,strong,sub,sup,tt,var,dd,dl,dt,li,ol,ul,fieldset,form,label,legend,button,table,caption,tbody,tfoot,thead,tr,th,td{margin:0;padding:0;border:0;font-weight:normal;font-style:normal;line-height:1;font-family:'Microsoft YaHei','SimHei',sans-serif
}
table{border-collapse:collapse;border-spacing:0
}
ol,ul{list-style:none
}
q:before,q:after,blockquote:before,blockquote:after{content:""
}
html{overflow-y:scroll;font-size:100%;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%
}
a:focus{outline:none 0
}
a:hover,a:active{outline:none 0
}
*{-ms-box-sizing:border-box;-o-box-sizing:border-box;box-sizing:border-box
}
a{text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0);-moz-tap-highlight-color:rgba(0,0,0,0);-ms-tap-highlight-color:rgba(0,0,0,0);-o-tap-highlight-color:rgba(0,0,0,0);tap-highlight-color:rgba(0,0,0,0)
}
article,aside,details,figcaption,figure,footer,header,nav,section{display:block
}
audio,canvas,video{display:inline-block;*display:inline;*zoom:1
}
audio:not([controls]){display:none
}
sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline
}
sup{top:-0.5em
}
sub{bottom:-0.25em
}
img{border:0;-ms-interpolation-mode:bicubic
}
button,input,select,textarea{font-size:100%;margin:0;vertical-align:baseline;*vertical-align:middle
}
button,input{line-height:normal;*overflow:visible
}
button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0
}
button,input[type="button"],input[type="reset"],input[type="submit"]{cursor:pointer;-webkit-appearance:button
}
input[type="search"]{-webkit-appearance:textfield;box-sizing:content-box
}
input[type="search"]::-webkit-search-decoration{-webkit-appearance:none
}
textarea{overflow:auto;vertical-align:top
}


